import React from 'react';
import Project from '../components/Project';
import { Container } from 'react-bootstrap';

import img1 from './../img/img1.jpg';





const Projects = () => {

    const projects = [
        { "image": img1, "alt": "PRO IMG", "name": "Pro_1", "description": "HTML CSS SCSS JS" },
        { "image": img1, "alt": "PRO IMG", "name": "Pro_1", "description": "HTML CSS SCSS JS" },
        { "image": img1, "alt": "PRO IMG", "name": "Pro_1", "description": "HTML CSS SCSS JS" },
        { "image": img1, "alt": "PRO IMG", "name": "Pro_1", "description": "HTML CSS SCSS JS" }
    ];

    

    return (
        <Container className='container'>

            <div className='projects'>

                {projects.map(project => <Project src={project.image} alt={project.alt} name={project.name} description={project.description} />)}
                
                
            </div>


        </Container>
    )
}



export default Projects;
